import logo from './logo.svg';
import './App.css';
import Card from './components/Card/Card';
import { useEffect, useState } from 'react';
import { Button } from 'antd';


function App() {
  const [videos, setVideos] = useState([]);
  const [loading, setLoading] = useState(false);
  
  useEffect(() => {
    async function main() {
      const req = await fetch(`https://youtubetrendinglist.herokuapp.com/videos`)
      const data = await req.json()
      console.log(data)
      setVideos(data);
    }
    main();
  },[]);
  const refresh = async () => {
    setLoading(true);
    await fetch("https://youtubetrendinglist.herokuapp.com/databaseupdate");
    setLoading(false);
    window.location.reload();

  }
  return (
    <div className="App">
      <div className="refresh">
        <Button onClick={refresh} loading={loading}>
          Refresh
        </Button>
      </div>
      {videos.map(video => <Card title={video.title} id={video._id} channelId={video.channelId} key={video._id} />)}
    </div>
  );
}

export default App;
