import { useEffect, useRef, useState } from "react";
import { useParams } from "react-router-dom"
import "./VideoPage.css"
import YouTube from "react-youtube";

const VideoPage = ()=>{
  const {id} = useParams()
  const [toggled, setToggled]=useState(false)
  const [details, setDetails] = useState();
  useEffect(()=>{
    const main = async()=>{
      const req = await fetch("https://youtubetrendinglist.herokuapp.com/videoDetails/?id="+id,{
      })
      const data = await req.json()
      setDetails(data)
      console.log(data)
    }
    main()
  },[id])
  const width = window.innerWidth*0.8
  const opts = {
      width: width,
      height: width*9/16,
      playerVars: {
        autoplay: 1,
      },
    };
    return (
      <div className="videoPage">
        <div className="player">
        <YouTube videoId={id} opts={opts}/>
        </div>
        <div className="videoTitle">
          {details?.videoData?.title}
        </div>
          <div className="meta">
            <div className="views">
              {details?.videoData?.views}
            </div>
            <div className="likes">
              {details?.videoData?.likes}
            </div>
          </div>
        <div className="channelBar">
          <div className="channel">
            <div className="channelIcon">
              <img src={details?.channelData?.thumbnails?.default.url} alt="channel"/> 
            </div>
            <div className="channelTitle">
              {details?.channelData?.title}
            </div>
            <div className="channelSubs">
              {details?.channelData?.subscriberCount || "Hidden"}
            </div>
          </div>
        </div>
        <h3>
          Channel Description
        </h3>
        <div className="channelDesc"
        dangerouslySetInnerHTML={{__html: details?.channelData?.description.replaceAll("\n","<br/>")}}>
        </div>
        <h3>
          Video Description
        </h3>
        <div className={`videoDesc ${toggled?"full":"half"}`}
        dangerouslySetInnerHTML={{__html: details?.videoData?.description.replaceAll("\n","<br/>")}}>
        </div>
        <button className="toggleDesc" onClick={()=>{
          setToggled(!toggled)
        }}>Show {toggled?"Less":"More"}</button>
      </div>
    )
}

export default VideoPage